.playlist {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.playlist_h1 {
    color: white;
    font-weight: 700;
    font-size: 30px;
    margin-top: 20px;
    margin-bottom: 30px;
}
.playlist_item {
    height: 70px;
    width: 500px;
    margin: 0 auto;
    margin-bottom: 30px;
    background-color: black;
    border-radius: 20px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.playlist_item h1 {
    width: 100px;
}
.playlist_item div {
    display: flex;
    align-items: center;
    height: 100%;
}
.playlist_item button {
    background-color: transparent;
}
.playlist_item .playlist_img {
    width: 50px;
    height: 50px;
    /* background-color: aqua; */
    margin-right: 10px;
}
.playlist_item p {
    width: 100px;
    color: grey;
}
.svg_gg {
    border: 1px solid white;
    border-radius: 50%;
    padding: 10px;
    padding-right: 8px;
    width: 40px;
    height: 40px;
}
.svg_gg * {
    fill: white;
}
.playlist_item button:hover .svg_gg {
    scale: 1.1;
}
@media(max-width: 500px) {
    .playlist {
        padding-left: 12px;
        padding-right: 12px;
    }
    .playlist_item {
        width: 100%;
    }
}
@media(max-width: 350px) {
    .playlist_img {
        width: 30px;
        height: 30px;
    }
    .playlist_item h1 {
        width: 70px;
        font-size: 13px;
    }
    .svg_gg {
        width: 30px;
        height: 30px;
    }
}