.bottom_panel {
    width: 100%;
    height: 100px;
    position: sticky;
    background-color: black;
    bottom: 0;
    color: white;
}
.timeline {
    top: 1px;
    position: relative;
    width: 100%;
    height: 10px;
    background-color: white;
}
.currentTime {
    height: 100%;
    background-color: red;
}

.bottom_panel button {
    margin-right: 20px;
    background-color: transparent;
    color: white;
}
.bottom_panel_two {
    display: flex;
    height: 100%;
    align-items: center;
    width: 100%;
    justify-content: space-around;
}
.svg_gg {
    border: 1px solid white;
    border-radius: 50%;
    padding: 10px;
    padding-right: 8px;
    width: 40px;
    height: 40px;
}
.svg_gg * {
    fill: white;
}
.controlls {
    height: 100%;
    display: flex;
    align-items: center;
}
.controlls input[type=range]::-webkit-slider-runnable-track {
    border-radius: 20px;
    background: transparent; 
  border-color: transparent;
  color: transparent;
}
.controlls input[type=range]::-webkit-slider-thumb {
    color: white;
    background-color: red;
  }
.controlls   input[type="range"]::-moz-range-progress {
    background-color: #43e5f7; 
}
.controlls  input[type="range"]::-ms-fill-lower {
    background-color: green; 
}
.controlls input[type="range"]::-ms-fill-upper {  
    background-color: #9a905d;
}

.about {
    width: 300px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.about h1 {
    font-weight: 700;
    font-size: 24px;
}
.about p {
    color: grey;

}
.controlls_svg {
    width: 25px;
    height: 25px;
}
.controlls_svg * {
    fill: white;
}
.controlls_svg:hover {   
    scale: 1.1;
}
@media(max-width: 500px) {
    .bottom_panel {
        height: 150px;
    }
    .bottom_panel_two {
        flex-direction: column;
    }
    .about h1 {
        font-size: 17px;
    }
}